<template>
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-card class="search-card" shadow="never">
        <el-form :model="searchForm" label-width="100px" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="转出主体">
                <el-select 
                  v-model="searchForm.subject_id" 
                  placeholder="请选择主体" 
                  clearable
                  filterable
                  style="width: 100%"
                >
                  <el-option 
                    v-for="subject in subjectList" 
                    :key="subject.id" 
                    :label="subject.company_name" 
                    :value="subject.id" 
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="收款人姓名">
                <el-input 
                  v-model="searchForm.payee_name" 
                  placeholder="请输入收款人姓名"
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="收款人账号">
                <el-input 
                  v-model="searchForm.payee_account" 
                  placeholder="请输入收款人账号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="转账时间">
                <el-date-picker
                  v-model="dateRange"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  format="YYYY-MM-DD HH:mm:ss"
                  value-format="YYYY-MM-DD HH:mm:ss"
                  style="width: 100%"
                  @change="handleDateRangeChange"
                  clearable
                />
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item>
                <div class="search-buttons">
                  <el-button type="primary" @click="handleSearch">
                    <el-icon><Search /></el-icon>搜索
                  </el-button>
                  <el-button @click="handleReset">
                    <el-icon><Refresh /></el-icon>重置
                  </el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </transition>

  <!-- 表头/按钮行 -->
  <div class="table-header ba-scroll-style">
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button type="primary" :icon="Money" @click="openTransferDialog">转账</el-button>
    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 转账确认对话框 -->
  <el-dialog
    v-model="confirmDialogVisible"
    title="确认转账信息"
    width="500px"
    :close-on-click-modal="false"
  >
    <div class="confirm-content">
      <el-descriptions :column="1" border>
        <el-descriptions-item label="转出主体">
          {{ getSubjectName(transferForm.subject_id) }}
        </el-descriptions-item>
        <el-descriptions-item label="收款人姓名">
          {{ transferForm.payee_name }}
        </el-descriptions-item>
        <el-descriptions-item label="收款人账号">
          {{ transferForm.payee_account }}
        </el-descriptions-item>
        <el-descriptions-item label="转账金额">
          <span style="color: #f56c6c; font-size: 18px; font-weight: bold;">
            ¥{{ transferForm.amount || '0.00' }}
          </span>
        </el-descriptions-item>
        <el-descriptions-item label="备注" v-if="transferForm.remark">
          {{ transferForm.remark }}
        </el-descriptions-item>
      </el-descriptions>
      <div class="confirm-tip">
        <el-alert
          title="请确认转账信息无误后再提交"
          type="warning"
          :closable="false"
          show-icon
        />
      </div>
    </div>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="confirmDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="confirmAndSubmit" :loading="transferSubmitLoading">
          确认转账
        </el-button>
      </span>
    </template>
  </el-dialog>

  <!-- 转账对话框 -->
  <el-dialog
    v-model="transferDialogVisible"
    title="转账"
    width="600px"
    :close-on-click-modal="false"
  >
    <el-form
      ref="transferFormRef"
      :model="transferForm"
      :rules="transferFormRules"
      label-width="120px"
    >
      <el-form-item label="转出主体" prop="subject_id">
        <el-select 
          v-model="transferForm.subject_id" 
          placeholder="请选择转出主体" 
          clearable
          filterable
          style="width: 100%"
        >
          <el-option
            v-for="subject in agentSubjectList"
            :key="subject.id"
            :label="subject.company_name"
            :value="subject.id"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="收款人姓名" prop="payee_name">
        <el-input 
          v-model="transferForm.payee_name" 
          placeholder="请输入收款人姓名" 
          clearable
        />
      </el-form-item>

      <el-form-item label="收款人账号" prop="payee_account">
        <el-input 
          v-model="transferForm.payee_account" 
          placeholder="请输入收款人账号" 
          clearable
        />
      </el-form-item>

      <el-form-item label="转账金额" prop="amount">
        <el-input 
          v-model="transferForm.amount" 
          placeholder="请输入转账金额" 
          clearable
          type="number"
          :min="0"
          :precision="2"
        >
          <template #prefix>¥</template>
        </el-input>
      </el-form-item>

      <el-form-item label="备注" prop="remark">
        <el-input 
          v-model="transferForm.remark" 
          type="textarea"
          :rows="3"
          placeholder="请输入备注信息（选填）" 
          clearable
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button @click="transferDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="showConfirmDialog">
          下一步
        </el-button>
      </span>
    </template>
  </el-dialog>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;"
    >
      <!-- 表格列定义 -->
      <el-table-column prop="transfer_no" label="转账单号" align="center" min-width="200"/>
      <el-table-column prop="subject_name" label="转出主体" align="center" min-width="200"/>
      <el-table-column prop="payee_name" label="收款人姓名" align="center" min-width="150"/>
      <el-table-column prop="payee_account" label="收款人账号" align="center" min-width="180"/>
      <el-table-column prop="amount" label="金额" align="center" min-width="120">
        <template #default="{ row }">
          ¥{{ row.amount || '0.00' }}
        </template>
      </el-table-column>
      <el-table-column prop="transfer_time" label="转账时间" align="center" min-width="180"/>
      <el-table-column prop="remark" label="备注信息" align="center" min-width="200" show-overflow-tooltip/>
      <el-table-column label="创建时间" align="center" min-width="180">
        <template #default="{ row }">
          <span v-if="!row.transfer_time">{{ row.created_at || '-' }}</span>
          <span v-else>-</span>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { Refresh, Search, Money } from '@element-plus/icons-vue';
import { ref, onMounted, computed } from 'vue';
import { ElMessage } from 'element-plus';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 使用表格插件来管理表格的状态、数据等
const {
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  isVisible: showSearch,
  toggleVisibility: toggleSearch
} = useTableDataPlugin("/api/v1/admin/single-royalty/transfer-order-list");

// 日期范围
const dateRange = ref([]);

// 搜索表单数据
const searchForm = ref({
  subject_id: '',
  payee_name: '',
  payee_account: '',
  start_time: '',
  end_time: ''
});

// 处理日期范围变化
const handleDateRangeChange = (value: any) => {
  if (value && value.length === 2) {
    searchForm.value.start_time = value[0];
    searchForm.value.end_time = value[1];
  } else {
    searchForm.value.start_time = '';
    searchForm.value.end_time = '';
  }
};

// 主体列表（用于搜索）
const subjectList = ref([]);
// 代理商主体列表（用于转账）
const agentSubjectList = ref([]);
// 用户信息
const userInfo = ref<any>({});
const isAgent = computed(() => userInfo.value?.is_agent === true);

// 转账对话框
const transferDialogVisible = ref(false);
const confirmDialogVisible = ref(false);
const transferSubmitLoading = ref(false);
const transferFormRef = ref(null);
const transferForm = ref({
  subject_id: '',
  payee_name: '',
  payee_account: '',
  amount: '',
  remark: ''
});

// 转账表单验证规则
const transferFormRules = {
  subject_id: [
    { required: true, message: '请选择转出主体', trigger: 'change' }
  ],
  payee_name: [
    { required: true, message: '请输入收款人姓名', trigger: 'blur' }
  ],
  payee_account: [
    { required: true, message: '请输入收款人账号', trigger: 'blur' }
  ],
  amount: [
    { required: true, message: '请输入转账金额', trigger: 'blur' },
    { 
      validator: (rule: any, value: any, callback: any) => {
        if (value && (isNaN(value) || parseFloat(value) <= 0)) {
          callback(new Error('转账金额必须大于0'));
        } else {
          callback();
        }
      }, 
      trigger: 'blur' 
    }
  ]
};

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取主体列表（用于搜索）
const getSubjectList = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/subject/list',
      method: 'GET',
      params: {
        page_size: 1000
      }
    });
    if (res.status && res.data.data) {
      subjectList.value = res.data.data;
    }
  } catch (error) {
    console.error('获取主体列表失败:', error);
  }
};

// 获取代理商的主体列表（用于转账）
const getAgentSubjectList = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/subject/list',
      method: 'GET',
      params: {
        page_size: 1000,
        status: 1 // 只获取启用的主体
      }
    });
    if (res.status && res.data.data) {
      // 如果是代理商，只显示自己的主体
      if (isAgent.value) {
        agentSubjectList.value = res.data.data.filter((subject: any) => 
          subject.agent_id === userInfo.value.agent_id
        );
      } else {
        agentSubjectList.value = res.data.data;
      }
    }
  } catch (error) {
    console.error('获取代理商主体列表失败:', error);
  }
};

// 打开转账对话框
const openTransferDialog = async () => {
  await getUserInfo();
  await getAgentSubjectList();
  
  // 重置表单
  transferForm.value = {
    subject_id: '',
    payee_name: '',
    payee_account: '',
    amount: '',
    remark: ''
  };
  
  transferDialogVisible.value = true;
  
  // 清除验证
  setTimeout(() => {
    transferFormRef.value?.clearValidate();
  }, 100);
};

// 获取主体名称
const getSubjectName = (subjectId: any) => {
  if (!subjectId) return '-';
  const subject = agentSubjectList.value.find((s: any) => s.id === subjectId);
  return subject?.company_name || '-';
};

// 显示确认对话框
const showConfirmDialog = async () => {
  try {
    // 先验证表单
    await transferFormRef.value.validate();
    
    // 关闭转账表单对话框
    transferDialogVisible.value = false;
    
    // 显示确认对话框
    confirmDialogVisible.value = true;
  } catch (error) {
    console.error('表单验证失败:', error);
    if (error !== false) {
      ElMessage.error('请填写完整的转账信息');
    }
  }
};

// 确认并提交转账
const confirmAndSubmit = async () => {
  try {
    transferSubmitLoading.value = true;
    
    const res = await request({
      url: '/api/v1/admin/single-royalty/transfer',
      method: 'POST',
      data: {
        subject_id: transferForm.value.subject_id,
        payee_name: transferForm.value.payee_name,
        payee_account: transferForm.value.payee_account,
        amount: parseFloat(transferForm.value.amount),
        remark: transferForm.value.remark || ''
      }
    });
    
    if (res.status) {
      ElMessage.success(res.msg || '转账成功');
      confirmDialogVisible.value = false;
      // 重置表单
      transferForm.value = {
        subject_id: '',
        payee_name: '',
        payee_account: '',
        amount: '',
        remark: ''
      };
      refreshData();
    } else {
      ElMessage.error(res.msg || '转账失败');
    }
  } catch (error) {
    console.error('转账失败:', error);
    if (error !== false) {
      ElMessage.error('转账失败');
    }
  } finally {
    transferSubmitLoading.value = false;
  }
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
};

// 处理重置
const handleReset = () => {
  dateRange.value = [];
  searchForm.value = {
    subject_id: '',
    payee_name: '',
    payee_account: '',
    start_time: '',
    end_time: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 在组件挂载时加载数据
onMounted(async () => {
  await getUserInfo();
  await getSubjectList();
  
  // 设置初始搜索参数
  searchParams.value = { ...searchForm.value };
  
  loadData();
});
</script>

<style scoped>
.search-card {
  margin-bottom: 20px;
}

.search-form {
  padding: 10px 0;
}

.search-buttons {
  display: flex;
  gap: 10px;
}

.confirm-content {
  padding: 10px 0;
}

.confirm-tip {
  margin-top: 20px;
}
</style>


